@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700;900&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
   /*  color: #05060f; */
}

.container{
    width: 1500px;
    max-width: 90%;
    height: 100%;
    padding: 0 50px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}


.amplify-form-section{
    width: 600px !important;
    display: inline-block;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(221, 221, 221);
    margin-bottom: 0 !important;
}

.amplify-section-header{
    font-size: 25px;
    font-weight: 700;
    color: #151f2d !important;
    background-color: #fff !important;
    padding: 25px 15px !important;
    border-bottom: 1px solid rgb(221, 221, 221) !important;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    text-align: center;
}

.amplify-section-body{
    padding: 0px 15px !important;
}

.amplify-section-body > div:first-child{
    height: 450px !important;
}

.amplify-section-body div::-webkit-scrollbar{
    padding: 15px 0 !important;
    display: none;
}

.amplify-section-body div > div{
    width: 450px !important;
    max-width: 100% !important;
    padding: 20px 15px;
    margin: 10px 0;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 0.4px;
    line-height: 1.5;
    font-size: 15px !important;
}

.amplify-section-body div div:nth-child(odd){
    float: left;
    background: #151f2d;
    color: #fff;
    border-radius: 10px 10px 10px 0;
}

.amplify-section-body div div:nth-child(even){
    float: right;
    background: #06f;
    color: #fff !important;
    border-radius: 10px 10px 0 10px;
}





.amplify-section-footer{
    border-top-left-radius: 0px !important;
    border-top-right-radius: 0px !important;
}

.amplify-section-footer button{
    background-color: #06f !important;
    color: #fff !important;
    font-weight: 600;
    border: none !important;
}

.hero{
    width: 100%;
    height: 50vh;
   
}

.header_image{
    background: linear-gradient( #0066ff8c, #151f2db0 ),  url(/static/media/background.57fd86d3.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center; 
    top: 0;
    left: 0;
    position: -webkit-sticky;
    position: sticky;
}

.hero > .container > h1{
    font-size: 58px;
    font-weight: 800;
    letter-spacing: 2px;
    color: #fff;
    position: -webkit-sticky;
    position: sticky;
}

.post-hero{
    width: 100%;
    min-height: 50vh;
    padding: 15px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.post-hero h2{
    padding: 15px;
}

.post-hero > .container{
    -webkit-flex-direction: column;
            flex-direction: column;
}

.post-hero > .container > .bot-description{
    padding: 15px 0;
    max-width: 800px;
    width: 100%;
    font-size: 18px;
    line-height: 1.5;
    letter-spacing: 0.5px;
}

.red-alert{
    color: crimson;
    font-weight: 500;
}

.bot-section{
    width: 100%;
    height: 80vh;
}

.bot-section > .container {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.section-info{
    width: 100%;
    text-align: center;
    background: #06f;
    border-radius: 10px;
    color: #fff;
    padding: 50px 0;
}

.graph_container{
    -webkit-flex-direction: column;
            flex-direction: column;
}

.graphSection{
    margin: 30px 0;
    padding: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.graphSection h2{
    padding: 25px;
    font-size: 22px;
    letter-spacing: 0.5px;
}

.graphSection h4{
    font-size: 14px;
